
<template>
    <div id="edit-employee">
        <h3>Edit Employee</h3>
        <form @submit.prevent="updateEmployee" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" id="employee_id" v-model="employee.employee_id" disabled>
                        <label>Employee ID</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" id="name" v-model="employee.name">
                        <label for="name">Employee Name</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" id="dept" v-model="employee.dept">
                        <label for="dept">Employee Department</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" id="position" v-model="employee.position">
                        <label for="position">Employee position</label>
                    </div>
                </div>

                <router-link to="/" class="btn grey">Cancel</router-link>
                <button class="btn orange" type="submit">Update</button>
            </form>
    </div>
</template>

<script>
import db from './firebaseInit'

export default {
    name: 'edit-employee',
    data (){
        return {
            employee: {
                employee_id: null,
                dept: null,
                position: null,
                name: null,
            }
        }
    },
    beforeRouteEnter (to, from, next) {
        db.collection('employees').where('employee_id', '==', to.params.employee_id).get()
        .then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.employee = doc.data();
                })
            })
        })
    },
    methods: {
        updateEmployee(){
            db.collection('employees').where('employee_id', '==', this.$route.params.employee_id).get()
            .then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    doc.ref.update(this.employee).then(res => {
                        this.$router.push({name: 'view-employee', params: { employee_id: this.employee.employee_id}})
                    });
        
                });
            });
        }
    }
}
</script>


