
<template>
    <div id="dashboard">
        <ul class="collection with-header">
            <li class="collection-header">
                <h4>Employees</h4>
            </li>
            <li class="collection-item" v-for="employee in employees" :key="employee.employee_id">
                <div class="chip">{{ employee.dept }}</div>
                {{ employee.employee_id}}:{{ employee.name}}
                <router-link :to="{name: 'view-employee', params: { employee_id: employee.employee_id}}">
                <i class="fa fa-eye"></i></router-link>
            </li>
        </ul>

        <div class="fixed-action-btn">
            <router-link to="/new" class="btn-floating btn-large red">
                <i class="fa fa-plus"></i>
            </router-link>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'

export default {
    name: 'dashboard',
    data (){
        return {
            employees: []
        }
    },
    created(){
        db.collection('employees').orderBy('dept').get()
        .then(querySnaphot => {
            querySnaphot.forEach(doc => {
                this.employees.push(doc.data());
            })
        });
    }
}
</script>


