
<template>
    <div id="new-employee">
        <h3>New Employee</h3>
        <div class="row">
            <form @submit.prevent="saveEmployee" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" id="employee_id" v-model="employee.employee_id">
                        <label for="employee_id">Employee ID</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" id="name" v-model="employee.name">
                        <label for="name">Employee Name</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" id="dept" v-model="employee.dept">
                        <label for="dept">Employee Department</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" id="position" v-model="employee.position">
                        <label for="position">Employee position</label>
                    </div>
                </div>

                <router-link to="/" class="btn grey">Cancel</router-link>
                <button class="btn" type="submit">Save</button>
            </form>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'

export default {
    name: 'new-employee',
    data (){
        return {
            employee: {
                employee_id: null,
                dept: null,
                position: null,
                name: null,
            }
        }
    },
    methods: {
        saveEmployee(){
            db.collection('employees').add(this.employee)
            .then(docRef => {
                this.$router.push('/');
            })
            .catch(error => console.log(error));
        }
    }
}
</script>


