
<template>
    <div id="view-employee">
        <ul class="collection with-header">
            <li class="collection-header">
                <h4>Name: {{ employee.name}}</h4>
            </li>
            <li class="collection-item">
                Employee ID: {{employee.employee_id}}
            </li>
            <li class="collection-item">
                Dept: {{employee.dept}}
            </li>
            <li class="collection-item">
                Position: {{employee.position}}
            </li>
        </ul>

        <router-link to="/">Back</router-link>
        <button class="btn red" @click="deleteEmployee(employee.employee_id)">Delete</button>
    
        <div class="fixed-action-btn">
            <router-link :to="{name: 'edit-employee', params: {employee_id: employee.employee_id}}" class="btn-floating btn-large red">
                <i class="fa fa-edit"></i>
            </router-link>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'

export default {
    name: 'view-employee',
    data (){
        return {
            employee: {
                id: null,
                employee_id: null,
                name: null,
                position: null,
                dept: null,
            }
        }
    },
    beforeRouteEnter (to, from, next) {
        db.collection('employees').where('employee_id', '==', to.params.employee_id).get()
        .then(querySnapshop => {
            querySnapshop.forEach(doc => {
                next(vm => {
                    vm.employee = doc.data()
                });
            });
            
        }) 
    },
    methods: {
        deleteEmployee(id){
            console.log(id);
            if(confirm("Are you sure do you want to delete this employee?")){
                db.collection('employees').where('employee_id', '==', id).get()
                .then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        doc.ref.delete();
                        this.$router.push("/");
                    })
                });
            }
        }
    }
}
</script>


